@charset "UTF-8";
/** Media Queries **/
.wc_decripciones {
  margin-top: 3rem;
}
.wc_decripciones h1 {
  font-size: 2.2rem;
  font-weight: 400;
  margin: 0 0 1.5rem 0;
}
.wc_decripciones h1 span {
  font-weight: 900;
}
@media (min-width: 768px) {
  .wc_decripciones h1 {
    font-size: 2.5rem;
  }
}
.wc_decripciones ul li {
  color: #000000;
}
.wc_decripciones p {
  color: #000000;
  text-align: justify;
  line-height: 1.5;
}
.wc_decripciones p:last-of-type {
  margin: 0;
}

.wc_planes {
  padding: 3.5rem 0;
  display: flex;
  flex-direction: column;
  align-items: center;
  gap: 2rem;
}
@media (min-width: 1024px) {
  .wc_planes {
    flex-direction: row;
    justify-content: center;
    align-items: flex-start;
  }
}
.wc_planes__item {
  width: 32rem;
  display: flex;
  flex-direction: column;
  justify-content: space-between;
}
.wc_planes__contenido {
  border: 1px solid;
  display: flex;
  flex-direction: column;
  justify-content: center;
}
.wc_planes__contenido--regular {
  border-color: #5b8fb5;
}
.wc_planes__contenido--clasico {
  border-color: #AC1112;
}
.wc_planes__contenido--premium {
  border-color: #f7841c;
}
.wc_planes__header {
  padding: 2rem;
  display: flex;
  align-items: center;
  justify-content: center;
  gap: 1rem;
  color: #000000;
}
.wc_planes__header--regular {
  background-color: rgba(91, 143, 181, 0.2);
}
.wc_planes__header--clasico {
  background-color: rgba(218, 88, 88, 0.3);
}
.wc_planes__header--premium {
  background-color: rgba(247, 132, 28, 0.4);
}
.wc_planes__icono i {
  font-size: 4rem;
}
.wc_planes__informacion {
  display: flex;
  flex-direction: column;
}
.wc_planes__precio {
  font-weight: 600;
  display: flex;
  flex-direction: row;
  gap: 0.5rem;
  align-items: center;
  line-height: 0.8;
  font-size: 4rem;
}
.wc_planes__precio del {
  font-size: 2rem;
  color: hsl(0, 0%, 119.0196078431%);
}
.wc_planes__precio ins {
  all: unset;
}
.wc_planes__precio ins bdi {
  display: flex;
  align-items: center;
  gap: 0;
}
.wc_planes__precio ins .woocommerce-Price-currencySymbol {
  font-size: 2.5rem;
}
.wc_planes__precio--regular {
  display: flex;
  flex-direction: row-reverse;
}
.wc_planes__precio--moneda {
  font-size: 2.5rem;
}
.wc_planes__precio--slogan {
  font-size: 2.2rem;
}
.wc_planes__frase {
  font-size: 1.2rem;
  line-height: 2;
  font-weight: 600;
  letter-spacing: 0.1rem;
  text-align: center;
  margin-top: 0.6rem;
}
.wc_planes__body {
  position: relative;
}
.wc_planes__plus {
  all: unset;
  position: absolute;
  top: -1.1rem;
  right: 0.5rem;
  line-height: 1;
  color: #FFFFFF;
  font-weight: 900;
  font-size: 1.2rem;
  padding: 0.5rem;
  border-radius: 0.3rem;
}
.wc_planes__plus:hover {
  cursor: pointer;
}
.wc_planes__plus--clasico {
  background-color: #AC1112;
}
.wc_planes__plus--premium {
  background-color: #f7841c;
}
.wc_planes__descripcion {
  display: none;
}
.wc_planes__descripcion--clasico {
  background-color: #da5858;
}
.wc_planes__descripcion--clasico i {
  background-color: #AC1112;
}
.wc_planes__descripcion--premium {
  background-color: #efe2d3;
}
.wc_planes__descripcion--premium i {
  background-color: #f7841c;
}
.wc_planes__descripcion.activo {
  display: block;
}
.wc_planes__vector--clasico {
  fill: #da5858;
}
.wc_planes__vector--premium {
  fill: rgb(241.0063829787, 118.9106382979, 8.4936170213);
}
.wc_planes__ul {
  padding: 1rem 3rem;
  line-height: normal;
}
.wc_planes__li {
  margin: 0.5rem 0;
  color: #000000;
}
.wc_planes__li i {
  border-radius: 50%;
  color: #FFFFFF;
}
.wc_planes__li i::before {
  width: 2rem;
  height: 2rem;
  display: flex;
  align-items: center;
  justify-content: center;
  font-size: 1.2rem;
}
.wc_planes__total {
  font-weight: 600;
  text-align: center;
  font-size: 1.8rem;
}
.wc_planes__total--clasico {
  color: #AC1112;
}
.wc_planes__total--premium {
  color: #f7841c;
}
.wc_planes__eira {
  padding: 0 3rem;
  display: flex;
}
.wc_planes__img {
  width: 7rem;
  z-index: 1;
}
.wc_planes__monto {
  margin-top: 1.5rem;
  flex-grow: 1;
  background-color: #FFFFFF;
  color: #000000;
  position: relative;
  font-weight: 600;
  line-height: normal;
  display: flex;
  justify-content: center;
  gap: 0.5rem;
  align-items: center;
  font-size: 3rem;
  padding: 0 2rem;
  border-bottom-right-radius: 1rem;
  border-top-right-radius: 1rem;
  flex-direction: column-reverse;
}
.wc_planes__monto::before {
  position: absolute;
  left: -3rem;
  display: block;
  content: "";
  width: 3rem;
  height: 100%;
  background-color: #FFFFFF;
}
.wc_planes__monto .woocommerce-Price-currencySymbol {
  font-size: 2.2rem;
}
.wc_planes__monto ins {
  all: unset;
}
.wc_planes__monto del {
  font-size: 2rem;
  color: #7d7d7d;
}
.wc_planes__monto--clasico {
  color: #AC1112;
}
.wc_planes__monto--premium {
  color: #f7841c;
}
.wc_planes__footer {
  text-align: center;
  padding: 2rem;
  border-top: 1px solid;
  border-color: rgba(0, 0, 0, 0.3);
}
.wc_planes__footer--regular {
  background-color: rgba(91, 143, 181, 0.1);
}
.wc_planes__footer--clasico {
  background-color: #da5858;
}
.wc_planes__footer--premium {
  background-color: #efe2d3;
}
.wc_planes__boton {
  background-color: #00615A;
  padding: 1.5rem 4rem;
  color: #FFFFFF !important;
  margin-top: 2rem;
  font-size: 2rem;
  font-weight: 700;
  border: none;
  transition-property: background-color;
  transition-duration: 0.3s;
  text-align: center;
  display: inline-block;
  width: 100%;
  border-radius: 1rem;
}
@media (min-width: 768px) {
  .wc_planes__boton {
    width: auto;
  }
}
.wc_planes__boton:hover {
  background-color: rgb(0, 46, 42.6804123711);
  color: #FFFFFF;
  cursor: pointer;
}
.wc_planes__boton {
  padding: 0.8rem 2rem;
  font-size: 1.5rem;
  width: auto;
  margin: 0;
}

